import Home from "./Pages/Home";
import Signup from './Pages/Signup';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import Signin from "./Pages/Signin";
import Member from "./Pages/Member";
import PaymentSuccess from "./Pages/PaymentSuccess";
import Workoutvideos from "./Pages/Workoutvideos";
import About from "./Pages/About";
import InstructorPage from "./Pages/InstructorPage";
import Contact from "./Pages/Contact";
import Playlist from "./Pages/Playlist";
import MicroCalculator from "./Pages/MicroCalculator";
import Recipes from "./Pages/Recipes";

function App() {
  return (
    <Router>
      <Switch>
        <Route component={Recipes} path='/recipes'/>
        <Route component={MicroCalculator} path='/micro-calculator'/>
        <Route component={Playlist} path='/playlist'/>
        <Route component={Contact} path='/contact'/>
        <Route component={InstructorPage} path='/instructors'/>
        <Route component={About} path='/about'/>
        <Route component={Workoutvideos} path='/workout-videos'/>
        <Route component={PaymentSuccess} path='/payment-success'/>
        <Route component={Member} path='/membership'/>
        <Route component={Signin} path='/signin'/>
        <Route component={Signup} path='/signup'/>
        <Route component={Home} path='/' />
      </Switch>
    </Router>
  );
}

export default App;
